.nav-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid lightblue;
    background-color: lightseagreen;
    align-items: center;
    width: 100%;

}

.foodies-heading {
    font-size: 28px;
    font-family: 'Roboto';
    font-weight: bold;
    color: #FF007F;
    padding: 10px;
}

.tabs-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.tab-container {
    display: flex;
    flex-direction: row;
}



.tab-container li {
    padding: 10px;
    list-style-type: none;
    font-size: 20px;
    font-family: 'Roboto';
    font-weight: bold;
    color: #FF007F;
}

.logout-button {
    padding: 12px;
    font-size: 15px;
    font-family: 'Roboto';
    font-weight: bold;
    color: #ffffff;
    background-color: #555555;
    margin: 10px;
    border-width: 0px;
    border-radius: 5px;
}