.Main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}


.Login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 400px;
    border: 1px solid lightblue;
    box-shadow: 10px 10px 5px lightblue;
    padding: 10px;
}

.login-heading {
    font-size: 30px;
    font-family: 'Roboto';
    font-weight: bold;
    color: #000000;
    padding: 5px;
}


.form-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

}

.input-container {
    display: flex;
    flex-direction: column;
}

.input-container>label {
    font-size: 20px;
    font-family: "Roboto";
    font-weight: bold;
    text-align: left;
    padding: 2px;
}

.input-container>input {

    width: 300px;
    padding: 10px;
}